@import url('https://fonts.googleapis.com/css2?family=Alef:wght@400;700&display=swap');

* {
    font-family: 'Alef', sans-serif;
    direction: rtl;
}

.grid-container {
    text-align: center;
    flex-grow: 1;
    margin: 5rem auto;
    width: 80%;
}

.paper {
    width: 300px;
    height: auto;
    min-height: 110px;
    margin: 0 auto;
    padding: 1.5rem 0;
    border-bottom: 3px solid dodgerblue;
    transition: all .2s !important;
}

.paper:hover {
    margin: -10px auto;
}

.heading {
    font-size: 1.7rem;
    font-weight: bold;
}

.number {
    display: block;
    font-size: 2.3rem;
    padding: 0;
    margin: 0;
}