@import url(https://fonts.googleapis.com/css2?family=Alef:wght@400;700&display=swap);
.container {
    margin: 3rem auto;
}

.MuiAutocomplete-inputRoot .MuiAutocomplete-input {
    direction: ltr;
    padding-left: 1rem !important;
}

.MuiAutocomplete-option {
    direction: ltr;
    padding-left: 1rem !important;
}
* {
    font-family: 'Alef', sans-serif;
    direction: rtl;
}

.grid-container {
    text-align: center;
    flex-grow: 1;
    margin: 5rem auto;
    width: 80%;
}

.paper {
    width: 300px;
    height: auto;
    min-height: 110px;
    margin: 0 auto;
    padding: 1.5rem 0;
    border-bottom: 3px solid dodgerblue;
    transition: all .2s !important;
}

.paper:hover {
    margin: -10px auto;
}

.heading {
    font-size: 1.7rem;
    font-weight: bold;
}

.number {
    display: block;
    font-size: 2.3rem;
    padding: 0;
    margin: 0;
}

.header {
    width: 80% !important;
    height: 20vh;
    display: block;
    margin: 0 auto;
    color: azure;
    font-size: 2.5rem;
    border-radius: .5rem;
}
